<script >
	import Bus from '../../util/eventBus.js'
	
	export default{
		props:{
			MonthData:Object,
		},
		data(){
			return{
				week_O:['M','T','W','T','F','S','S'],
				Month:['','JANUARY','FEBRUARY','MARCH','APRIL','MAY','JUNE','JULY','AUGUST','SEPTEMBER','OCTOBER','NOVEMBER','DECEMBER'],
				year:new Date().getFullYear(),
				month:new Date().getMonth() + 1,
				lastMonth:[],
				thisMouth:[],
				nextMouth:[],
				weekstart:0 ,
				getData:{},
			}
		},
		created() {
			// this.monthDay(2021,8)
		 //  Bus.$on('MonthData',val=>{
			// 	this.getData = val                            
		 //  })    
			// console.log('getData'+ this.getData);
		},
		methods:{
			//获取月份数据
			monthDay(y,month){
				let dates = []
				let m = Number(month)
				let firstDayofMouth = new Date(y, m-1, 1).getDay() //当前第一天是周几
				let lastDayofMonth = new Date(y, m, 0).getDate() //当月最后一天
				let lastDayofLastMonth = new Date(y, m-1, 0).getDate() //上个月最后一天
				// startDay 每个月第一周周初有几天是上个月的
				let startDay = (() => {
						if (firstDayofMouth > 0 && firstDayofMouth < 7) {
							return firstDayofMouth-1;
						}else if(firstDayofMouth == 0){
							return 6;
						}
				})();
				// endDay 结束还有多少天是下个月的
				let endDay = (() => {
						return 42 - lastDayofMonth - startDay 
				})();
				//上个月剩余的天数数组
				let lastRemainingDays = []
				for(let i=lastDayofLastMonth,j=0; i>(lastDayofLastMonth - startDay);i--,j++){
					lastRemainingDays[j] = i
				}
				lastRemainingDays = lastRemainingDays.reverse()
				this.lastMonth = lastRemainingDays.filter(n=>n)
				console.log('上个月剩余的天数数组' + this.lastMonth);
				//下个月剩余的天数数组
				let daysNextMonth =  []
				for(var h=1; h<= endDay; h++){
					daysNextMonth[h] = h
				}
				this.nextMouth = daysNextMonth.filter(n=>n)
				console.log('下个月剩余的天数数组' + this.nextMouth);
				
				// 本月天数数组
				let thisDaysMonth =  []
				for(var k=1; k<= lastDayofMonth; k++){
					thisDaysMonth[k] = k
				}
				this.thisMouth = thisDaysMonth.filter(n=>n)
				console.log('本月剩余的天数数组' + this.thisMouth);
			},
			switch_monthsub(){
				console.log('减一');
				let _this = this
				this.month -= 1
				console.log(this.year+'年'+this.month+'月');
				if(this.month < 1){
					this.year -= 1
					this.month = 12
					console.log(_this.year+'年'+_this.month+'月');
					this.monthDay(_this.year,_this.month)
				}
				this.monthDay(_this.year,_this.month)
			},
			switch_monthPlus(){
				console.log('加一');
				let _this = this
				this.month += 1
				console.log(this.year+'年'+this.month+'月');
				if(this.month > 12){
					_this.year += 1
					_this.month = 1
					console.log(_this.year+'年'+_this.month+'月');
					this.monthDay(_this.year,_this.month)
				}
				this.monthDay(_this.month,_this.month)

			},
			
			//选中当前日期
			getThisDay(e){
				let date = new Date().getDate()
				console.log('getThisDay'+date);
				if(e == date){
					return true
				}else{
					return false
				}
			},
			//奖杯显示的位置
			getJiangbei(e){
				let date = new Date().getDate()+7
				console.log('我是'+date);
				if(e == date){
					return true
				}else{
					return false
				}
			}
		},
		beforeCreate() {
			// this.monthDay(2021,8) 
			// Bus.$on('MonthData',val=>{
			// 	this.getData = val                            
			// })     
			// console.log('111');
			// console.log(this.getData);
		},
		mounted() { 
			this.monthDay(this.year,this.month)
			// console.log('1');
			this.getThisDay()
			this.getJiangbei()
		}
	}
</script>




